/**
 * Manage API response promise
 * @param {string} url - URL of the API
 * @returns
 */
const APIResponsePromise = async (url = '') => {
	// Get API token from multiple possible sources
	let apiToken = '';

	// Method 1: WordPress localized data (from admin settings)
	if (window.gutenbergStarterData?.movieApiToken) {
		apiToken = window.gutenbergStarterData.movieApiToken;
	}

	// Method 2: Environment variables (fallback)
	if (!apiToken && window.envVars?.MOVIE_API_KEY) {
		apiToken = window.envVars.MOVIE_API_KEY;
	}

	if (!apiToken) {
		throw new Error(
			'Movie API token is not configured. Please set it in the plugin settings or environment variables.'
		);
	}

	const options = {
		method: 'GET',
		headers: {
			accept: 'application/json',
			Authorization: `Bearer ${apiToken}`,
		},
	};
	const getMovieAPIResponse = await fetch(url, options);
	const getMovieAPIResponseJSON = await getMovieAPIResponse.json();
	if (getMovieAPIResponseJSON.success === false) {
		throw new Error(getMovieAPIResponseJSON.status_message);
	}
	return getMovieAPIResponseJSON;
};

export default APIResponsePromise;
